export default class User {
    #id
    #email
    #password
    #orders

    static #idRaw = 1

    // Realiza uma srie de verificaes para saber se o usurio foi pego do banco de dados 
    //ou se  um novo usurio. Se for um novo usurio a classe cria um ID, cartId e lista de pedidos, 
    //se for um usurio existente ele pega as informaes prontas passadas a partir do banco de dados
    constructor(id=null, isSeller, name, email, password, cartId=null, orders=null) {
        this.#id = id === null ? `${name.slice(0,2)}${User.#idRaw}` : id
        this.isSeller = isSeller
        this.name = name
        this.#email = email
        this.#password = password
        this.cartId = cartId === null ? `Ca${User.#idRaw}` : cartId
        this.#orders = orders === null ? [] : orders
        User.#idRaw++
    }

    get id () {
        return this.#id
    }

    get email () {
        return this.#email
    }

    password (caller) {
        if (caller instanceof Auth) {
            console.log("Acesso permitido")
            return this.#password
        } else {
            throw new Error("Acesso no autorizado")
        }
    }

    get orders () {
        return this.#orders
    }

    order (order) {
        this.#orders.push(order)
    }

    toJSON () {
        return {
            id: this.#id,
            isSeller: this.isSeller,
            name: this.name,
            email: this.#email,
            password: this.#password,
            cartId: this.cartId,
            orders: this.#orders
        }
    }
}