import Table from "./Table.js"

export default class UsersTable extends Table {
    constructor () {
        super("users")
    }

    async findEmail (email) {
        const allUsers = await this.getAll()
        const user = allUsers.find(user => user.email === email)
        if (user) {
            return user
        } else {
            return false
        }
    }

    async getUserByEmail (email) {
        const userRequired = await this.findEmail(email)
        return userRequired
    }

    async send (userObj) { // Sobreescreve funo de POST e verifica se o email a ser cadastrado j existe
        const emailExists = await this.findEmail(userObj.email)

        if (emailExists) {
            return {
                status: "ERROR",
                message: "Este email j est em uso"
            }
        }
        const response = await super.send(userObj) // Chama o mtodo original aps a verificao ter sido feita
        return response
    }
}
